IG$.MainPanel = $s.extend($s.panel, {
	margin: '0 0 0 0',
	resizeTabs: true,
	minTabWidth: 100,
	// tabWidth: 135,
	
	enableTabScroll: true,
	activeTab: 0,
	layout: {
		type: "card",
		deferredRender: false
	},
	
	deferredRender: false,
	
	hidemode: "offset",
	
	tabPosition : 'top',
	removePanelHeader: true,

	plain: false,

	minTabWidth: undefined,
	maxTabWidth: undefined,
	___tabseq: 0,
	
	customAddTab: function(uid, ext_panel) {
		var me = this,
			tab = me.getComponent(uid);
			
		if (tab)
		{
			IG$.changeApp(2);
			me.setActiveTab(tab);
		}
		else
		{
			IG$.changeApp(2);
			ext_panel.id = uid;
			tab = me.add(ext_panel);
			me.setActiveTab(tab);
		}
	},
	
	navigateApp: function(uid, itemtype, itemname, itemaddr, baddhistory, writable, option, toption) {
		var me = this,
			key,
			mkey = itemtype + "_" + (uid ? uid : me.___tabseq++),
			tab = this.getComponent(mkey);
			
		if (tab)
		{
			IG$.changeApp(2);
			
			this.setActiveTab(tab);
			
			if (toption && tab.applyOption)
			{
				tab.applyOption.call(tab, toption);
			}
			else if (itemtype == "dashboard")
			{
				var teditor = this.getComponent("dashboardedit" + "_" + uid);
				if (teditor)
				{
					if (tab.uid && tab.uid.length > 0)
					{
						tab.procRunDashboard.call(tab);
					}
				}
			}
		}
		else
		{
			var callback = new IG$.callBackObj(this, function() {
					this.navigateApp(uid, itemtype, itemname, itemaddr, baddhistory, writable, option, toption);
				}),
				pitem = IG$.createAppPanel(uid, itemtype, itemname, itemaddr, writable, option || null, callback);
			
			if (pitem)
			{
				pitem.address = itemaddr;
				pitem.title = IG$.getTabTitle(itemname);
				
				if (option)
				{
					for (key in option)
					{
						pitem[key] = option[key];
					}
				}
				
				if (toption)
				{
					for (key in toption)
					{
						pitem[key] = toption[key];
					}
				}
				
				if (pitem.isWindow == true)
				{
					baddhistory = false;
					pitem.title = IG$.getTabTitle(itemname) + " (" + IRm$.r1("D_" + itemtype.toUpperCase()) + ")";
					
					if (!IG$.ps[pitem.address])
					{
						IG$.ps[pitem.address] = pitem;
						pitem.on("close", function() {
							delete IG$.ps[pitem.address];
						});
						pitem.show();
					}
				}
				else
				{
					IG$.changeApp(2);
					pitem.id = mkey;
					this.selectedIndex = this.selectedIndex < 0 ? 0 : this.selectedIndex;
					var p = this.add(pitem);
					this.setActiveTab(p);
				}
			}
			else
			{
				baddhistory = false;
			}
		}
		
		if (baddhistory == true && uid && itemtype)
		{
			// lastHist = 'lang=' + useLocale + '&p1=' + itemtype + '&p2=' + uid;
			lastHist = "&p1=" + itemtype + "&p2=" + uid;
			setTimeout(function() {
				hist.addHistory(lastHist);
			}, 10);
		}
	},
	
	initRecentVisit : function(){
		var me = this,
			btn1 = $("#mbutton1"),
			idv_recitems = $("#idv_recitems"),
			welcome = $("#welcome"),
			items,
			pg_links;
			
		me.mviewer = {
			mode: "recent",
			mrecenttext: btn1.text()
		};
		
		btn1.unbind("click");
		
		btn1.bind("click", function() {
			me.loadRecentVisit.call(me);
		});
		
		var go_navigator = function() {
			var mexplorer = me.down("[name=mexplorer]");
			if (mexplorer && !mexplorer.isVisible())
			{
				IG$.changeApp(1);
			}
			else if (IG$.m_explorer && ig$.app_tmpl && ig$.app_tmpl.mainview && ig$.app_tmpl.mainview.navitree && ig$.app_tmpl.mainview.navitree.hidden !== false)
			{
				IG$.changeApp(1);
			}
		}
		
		items = [
			{
				title: "Step by Step",
				cls: "getting_started",
				handler: function() {
					IG$.procMenuCommand("CMD_APP_WIZARD");
				}
			},
			{
				title: "Navigator",
				cls: "navigator",
				handler: function() {
					go_navigator();
				}
			},
			/*
			{
				title: "Excel file loading",
				cls: "fileupload",
				hidden: ig$.features["ig_dataset"] !== false,
				handler: function() {
					IG$.procMenuCommand("CMD_DATASET");
				}
			},
			{
				title: "Database connection",
				cls: "database-con",
				hidden: ig$.features["ig_dbcon"] !== false,
				handler: function() {
					IG$.procMenuCommand("CMD_CONNECT_DB");
				}
			},
			*/
			{
				title: "About",
				cls: "about_app",
				handler: function() {
					IG$.procMenuCommand("CMD_ABOUT");
				}
			}
		];
		
		/*
		if (ig$.main_navigator === true)
		{
			go_navigator();
		}
		*/
		
		pg_links = $("#pg_links", welcome);
		pg_links.empty();
		
		$.each(items, function(i, item) {
			if (item.hidden)
				return;
			
			var el = $("<div class='intro-pg-column'><div class='intro-figure'><div class='image' title='" + item.title + "'><span class='" + item.cls + "'></span></div><div class='intro-figure-caption'><span class='intro-links'>" + item.title + "</span></div></div></div>");
			
			el.appendTo(pg_links);

			if (el.length > 0 && item.handler)
			{
				$(".image", el).bind("click", function() {
					item.handler();
				});
				
				$(".intro-links", el).bind("click", function() {
					item.handler();
				});
			}
			
			item.el = el;
		});
		
		me.__pg_linksitems = items;
				
		me.updateLoginInfo();
		
		me.loadRecentVisit();
	},
	
	initAdminMenu: function() {
		var dmgr = IG$.dmgrmenu,
			dom = dmgr.body.dom,
			body = $(".ig-mgr-body", dom),
			cl = $(".ig-mgr-panel-close", dom),
			menus = ig$.mainmenu.mainmenu,
			menu,
			i,
			li,
			tli,
			tul;
			
		body.empty();
		
		cl.bind("click", function() {
			IG$.procMenuCommand("CMD_HOME");
		});
		
		if (!menus)
			return;
				
		$.each(menus, function(i, menu) {
			tul = $("<ul class='ig-mgr-group'></ul>").appendTo(body);
			
			menu.tul = tul;
			menu.tul.hide();
			
			menu.menus && $.each(menu.menus, function(j, m) {
				var li = $("<li class='ig-mgr-group-item " + (m.acls || "") + " animate-box fadeInLeft animated'><div class='ig-mgr-title'><span class='icon'></span><h3>" + (IRm$.r1(m.rcs) || m.text) + "</h3></div><ul class='ig-mgr-menu'></ul></li>").appendTo(tul),
					tli = $(".ig-mgr-menu", li);
					
				$.each(m.items, function(k, mi) {
					var btn = $("<li class='ig-mgr-menu-item " + (mi.acls || "") + "'>" + (IRm$.r1(mi.rcs) || mi.text) + "</li>").appendTo(tli);
					btn.bind("click", function() {
						if (mi.handler)
						{
							mi.handler.call(this);
						}
						else if (mi.cmd == "logout")
						{
							IG$.showLogout(new IG$.callBackObj(this, IG$.showLoginProc));
						}
						else if (mi.cmd)
						{
							IG$.procMenuCommand(mi.cmd);
						}
					});
				});
			});
		});
	},
	
	updateLoginInfo: function() {
		var llogin = $("#last_login"),
			ltime,
			lmsg,
			viewport = IG$.viewport,
			logininfo = IG$.dlgLogin ? IG$.dlgLogin.logininfo : null,
			cur_login_user_id,
			cur_login_user_nm,
			cur_login_host,
			last_login_time,
			last_login_host;
		
		$("#home_app_title").html(ig$.home_app_title || IRm$.r1("L_HOME_APP_TITLE"));
		$("#home_app_desc").html(ig$.home_app_desc || IRm$.r1("L_HOME_APP_DESC"));
		
		$("#mbutton1").html(IRm$.r1("L_T_RECENT_VISIT"))
		
		if (llogin && llogin.length > 0 && logininfo && IG$.dlgLogin.lastLogin)
		{
			last_login_time = $(".last_login_time", llogin);
			last_login_host = $(".last_login_host", llogin);
			cur_login_host = $(".cur_login_host", llogin);
			cur_login_user_id = $(".cur_login_user_id", llogin);
			cur_login_user_nm = $(".cur_login_user_nm", llogin);
			
			ltime = IG$.dlgLogin.lastLogin.lastaccesstime;
			cur_login_user_id.text(logininfo.userid);
			cur_login_user_nm.text(logininfo.username);
			
			if (ltime)
			{
				lmsg = IG$.formatDate(ltime);
				last_login_time.text(lmsg);
				last_login_host.text(IG$.dlgLogin.lastLogin.lastaccessaddr); //  + ", " + IG$.dlgLogin.lastLogin.lastaccesshost);
				cur_login_host.text(IG$.dlgLogin.lastLogin.accessaddr); //  + ", " + IG$.dlgLogin.lastLogin.accesshost);
			}
		}
	},
	
	loadRecentVisit: function() {
		var panel=this,
			idv_recitems = $("#idv_recitems"),
			req = new IG$._rpc$();
			
		req.init(panel, 
			{
				ack: "helpcontent",
				payload: {},
				mbody: {option: 'bookmarks'}
			}, panel, panel.rs_loadContent, null, null);
		req.send();
	},
	
	rs_loadContent: function(jdoc) {
		var panel=this,
			idv_recitems = $("#idv_recitems"),
			i,
			items = [], item, col = 3, columns = [],
			tmap = {},
			coldiv;
		
		idv_recitems.empty();
		
		if (idv_recitems)
		{
			for (i=0; i < col; i++)
			{
				coldiv = $("<div class='recent-columns'></div>").appendTo(idv_recitems);
				coldiv.css({"width": (100 / col) + "%", "float": "left"});
				columns.push({
					div: coldiv,
					di: false
				});
			}
			
			for (i=0; i < jdoc.length; i++)
			{
				item = jdoc[i];
				if (tmap[item.uid])
				{
					 tmap[item.uid].fatype = item.fatype == "F" ? item.fatype : tmap[item.uid].fatype;
				}
				else
				{
					tmap[item.uid] = item;
					items.push(item);
				}
			}
			
			items.sort(function(a, b) {
				var c = 0;
				
				if (a.type != b.type)
				{
					c = (a.type > b.type) ? 1 : -1;
				}
				
				return c;
			});
			
			panel.shortcuts = {};
			
			var pgroup = 0,
				pgname;
				
			$.each(items, function(i, item) {
				var uc, 
					ul,
					tl,
					utype = item.type.toUpperCase();
				
				if (i == 0)
				{
					uc = columns[0];
					ul = uc.div;
					$("<div class='recent-itemtype'><span>" + IRm$.r1("D_" + utype) + "</span></div>").appendTo(ul);
				}
				else if (pgname && pgname != item.type)
				{
					pgroup = (pgroup+1) % columns.length;
					uc = columns[pgroup];
					ul = uc.div;
					tl = $("<div class='recent-itemtype'><span>" + IRm$.r1("D_" + utype) + "</span></div>").appendTo(ul);
					if (uc.di)
					{
						tl.css({marginTop: "20px"});
					}
				}
				else
				{
					uc = columns[pgroup];
					ul = uc.div;
				}
				
				uc.di = true;
				
				var li = $("<div class='recent-row' title='" + item.nodepath + "'></div>");
				var sp = $("<span>" + item.name + (item.fatype == "F" ? "<span class='ig-fa-mark'></span>" : "") + "</span>");
				sp.bind("click", function() {
					IG$.navigateMenu(item.uid);
				});
				
				if (utype == "REPORT" || utype == "DASHBOARD")
				{
					sp.hover(
						function(e) {
							var bdata = utype.toLowerCase() + ":" + item.uid;
							IG$._scrshot(sp, bdata, true);
						}, function(e) {
							IG$._scrshot(sp, null, false);
						}
					);
				}
				
				li.append(sp);
				ul.append(li);
				
				pgname = item.type;
			});
		}
	},
	
	deferredRender : true,
	
	onAdd: function(item, index) {
		$s.tabpanel.prototype.onAdd.call(this, item, index);
	},
	
	//inherit docs
	initComponent: function() {
		var me = this,
			dockedItems = [].concat(me.dockedItems || []),
			activeTab = me.activeTab || (me.activeTab = 0),
			tabPosition = me.tabPosition,
			tbar;

		// Configure the layout with our deferredRender, and with our activeTeb
		me.layout = {
			owner: me,
			type: "card",
			deferredRender: me.deferredRender,
			itemCls: me.itemCls,
			activeItem: activeTab
		};

		IG$.mainPanelTabBar = new $s.tabbar({
			xtype: "tabbar",
			ui: me.ui,
			dock: "top",
			id: "ig-tabbar",
			cls: "ig-main-tabbar",
			flex: 1,
			orientation: "horizontal",
			plain: true, //me.plain,
			cardLayout: me.layout,
			tabPanel: me,
			padding: 0,
			margin: 0,
			bodyPadding: 0,
			listeners: {
				tabclick: function(tobj, id, ev) {
					IG$.changeApp(2);
				}
			}
		});
		
		me.tabBar = IG$.mainPanelTabBar;

		IG$.MainPanel.superclass.initComponent.call(this);
		
		me.setActiveTab = function(card) {
			IG$.changeApp(2);
			$s.tabpanel.prototype.setActiveTab.call(me, card);
		};
		me.getActiveTab = $s.tabpanel.prototype.getActiveTab;
		me.getTabBar = $s.tabpanel.prototype.getTabBar;
		me.onItemEnable = $s.tabpanel.prototype.onItemEnable;
		me.onItemDisable = $s.tabpanel.prototype.onItemDisable;
		me.onRemove = function(item, destroying) {
			$s.tabpanel.prototype.onRemove.call(me, item, destroying);
			
			var itemcnt = me.items.length;
			
			if (itemcnt == 0)
			{
				var explorer = IG$.m_explorer;
				IG$.changeApp(IG$.__atab);
			}
		}
		
		// We have to convert the numeric index/string ID config into its component reference
		//  activeTab = me.activeTab = me.getComponent(activeTab);

		// Ensure that the active child's tab is rendered in the active UI state
		if (activeTab) {
			me.tabBar.setActiveTab(activeTab.tab, true);
		}
	},
	
	listeners: {
		beforeadd: function(tobj, comp, index, eopts) {
			var me = this,
				tabbar = me.getTabBar(),
				maxtab = ig$.maxtapcount || 0,
				atab;
				
			if (maxtab > 0 && me.items.length >= maxtab)
			{
				// tabbar.remove.call(tabbar, tabbar.items.items[0]);
				// me.setActiveTab(me.items.items[0]);
				atab = me.items.items[0];
				// me.setActiveTab(atab);
				me.remove(atab);
				//atab.close();
				if (tabbar && tabbar.$ex && atab.tab && typeof(atab.tab.tabid) != "undefined")
				{
					tabbar.$ex.removeOption(atab.tab.tabid);
				}
			}
		}
	}
});