IG$.historyManager = function() {
	this.addHistory = function (key) {
		unFocus.History.addHistory(key);
	};
	
	this.hasMenu = function() {
		var allhash = unFocus.History.getCurrent();
		if (allhash && allhash != "")
		{
			var basehash = allhash.split('&'),
				p1, p2, i, k, val, s, sa;
			
			for (i=0; i < basehash.length; i++)
			{
				sa = basehash[i];
				s = sa.indexOf("=");
				if (s > 0)
				{
					k = sa.substring(0, s);
					val = sa.substring(s+1);
					
					switch (k)
					{
					case "lang":
						break;
					case "p1":
						p1 = val;
						break;
					case "p2":
						p2 = val;
						break;
					}
				}
			}
			
			if (p1 && p2)
				return true;
		}
		
		return false;
	};
	
	this.historyListener = function() {
		var chash = unFocus.History.getCurrent();
		
		if (chash != null && chash != "" && IG$.dlgLogin.logininfo)
		{
			IG$.navigateMenu(chash);
		}
		
		return chash;
	};
	
	unFocus.History.addEventListener('historyChange', this.historyListener);
}

IG$.navigateMenu = function(allhash)
{
	if (allhash && allhash.length > 0 && lastHist != allhash)
	{
		if(IG$.checkUID(allhash))
		{
			p2 = allhash;
		}
		else
		{
			var basehash = allhash.split('&'),
				p1, p2, i, k, val, s, sa;
			
			for (i=0; i < basehash.length; i++)
			{
				sa = basehash[i];
				s = sa.indexOf("=");
				if (s > 0)
				{
					k = sa.substring(0, s);
					val = sa.substring(s+1);
					
					switch (k)
					{
					case "lang":
						break;
					case "p1":
						p1 = val;
						break;
					case "p2":
						p2 = val;
						break;
					}
				}
			}
		}
		
		if (p2)
		{
			IG$.navigateMenuByUID(p2, p1);
		}
	}
}

IG$.navigateMenuByUID = function(uid, hashtype, param, bhist)
{
	var req = new IG$._rpc$();
	
	req.init(null, 
		{
			ack: "helpcontent",
			payload: {uid: uid},
			mbody: {option: "translate"}
		}, null, function(jdoc) {
			var node = jdoc[0];
			
			var itemuid = node.uid,
				typename = node.type.toLowerCase(),
				itemname = node.locale_name || node.name,
				itemaddr = node.nodepath,
				writable = node.manage || node.writable,
				isexport = false; 
				
			if (typename == 'dashboard' && hashtype == 'dashboardedit')
			{
				typename = hashtype;
			}
			else if (typename == "export")
			{
				isexport = true;
				typename = "report";
			}
			
			if (isexport == true)
			{
				var doc = $(document),
					rmain = $("#main"),
					toption = {
						renderTo: rmain[0],
						minHeight: doc.height(),
						address: itemaddr,
						closable: false,
						_ts_: false,
						hiddenstatusbar: true,
						hiddentoolbar: true,
						header: false,
						title: IG$.getTabTitle(itemname)
					};
//					option = null;
					
					pitem = IG$.createAppPanel(itemuid, typename, itemname, itemaddr, writable, toption);
					pitem.on({
						_lc0: function() {
							
						},
						_lc1: function() {
							var cs = pitem.calcSize.call(pitem);
							pitem.setSize(cs.width, cs.height);
						},
						resize: function(t, width, height, oldWidth, oldHeight, eOpts) {
							var dbody = $(document.body);
							
							IG$.j$ext._w(rmain, width);
							IG$.j$ext._h(rmain, height);
							IG$.j$ext._w(dbody, width);
							IG$.j$ext._h(dbody, height);
						}
					});
					pitem.updateLayout();
					pitem.setSize(doc.width(), doc.height());
				
				rmain.css({minHeight: (doc.height() + "px")});
			}
			else
			{
				IG$.mainPanel.navigateApp.call(IG$.mainPanel, itemuid, typename, itemname, itemaddr, (typeof(bhist) == "undefined" ? true : bhist), writable, null, param);
			}
		}, null);
	req.send();
}

