IG$._n5/*mec_sesst*/ = function() {
	var session = ig$.session,
		t = ig$.session_expire * 60,
		inrequest = false,
		reqtimer = -1;
	
	if (session)
	{
		$(document).bind("mousemove", function() {
			session.count = 0;
			
			if (inrequest == false)
			{
				if (reqtimer > -1)
				{
					clearTimeout(reqtimer);
					reqtimer = -1;
				}
				
				reqtimer = setTimeout(function() {
					inrequest = true;
					var req = new IG$._rpc$();
					req.init(null, 
						{
							ack: "helpcontent",
							payload: {mode: "write"},
							mbody: {option: "wake"}
						}, null, function(xdoc) {
							inrequest = false;
							session.count = 0;
						}, null);
					req.send();
				}, 2000);
			}
		});
		
		session.timer = setInterval(function() {
			var r = t - session.count,
				disp = "",
				mm = 0,
				m;
			
			if (r > 60)
			{
				mm = Math.floor(r / 60);
				m = r - (mm * 60);
			}
			else
			{
				m = r;
			}
			
			if (mm > 0)
			{
				disp = mm + " min " + m + " sec";
			}
			else
			{
				disp = m + " sec";
			}
			
			if (r < 300)
			{
				session.text.addClass("warn-sess-timer");
			}
			else
			{
				session.text.removeClass("warn-sess-timer");
			}
			
			session.text.html(disp);
			
			if (r < 0)
			{
				clearInterval(session.timer);
				IG$.showLogout(new IG$.callBackObj(this, IG$.showLoginProc));
			}
			
			session.count += 1;
		}, 1000);
	}
}

