IG$.procMenuCommand = function(cmd, opt)
{
	var dlgpop,
		panel,
		itemname,
		itemid,
		cmdl = cmd.toLowerCase(),
		modname,
		popt = {},
		modscr = "igcm",
		// sc,
		mp = IG$.mainPanel || null,
		btype;
	
	switch (cmd)
	{
	case "CMD_PASSWORD_CHANGE":
		dlgpop = new IG$.passwordMgr();
		IG$.checkLogin(null, dlgpop);
		break;
	case "CMD_PREF":
		dlgpop = new IG$.userpref();
		IG$.checkLogin(null, dlgpop);
		break;
	case "CMD_MAKE_WORKSPACE":
		dlgpop = new IG$.makeItem({
			itemtype: "Folder",
			parentnodepath: "/",
			parentuid: "/"
		});
		IG$.checkLogin(null, dlgpop);
		break;
	case "CMD_SW_APP":
		dlgpop = new IG$.workspace_switcher({
		});
		IG$.checkLogin(null, dlgpop);
		break;
	case "CMD_SERVER_CONFIG":
		break;
	case "CMD_HELP_MGR":
		modname = "helpManage";
		itemid = cmdl;
		itemname = IRm$.r1("I_HELP_MGR");
		break;
	case "CMD_LOCALE":
		modname = "resourcemgr";
		itemid = cmdl;
		itemname = IRm$.r1("I_LOC_MGR");
		break;
	case "CMD_DLOCALE":
		modname = "resourcemgr";
		itemid = cmdl;
		popt.dicmode = 1;
		itemname = IRm$.r1("I_DLOC_MGR");
		break;
	case "CMD_META_IMP":
		modname = "metaImport";
		itemid = cmdl;
		itemname = IRm$.r1("I_META_IMP");
		break;
	case "CMD_META_EDIT":
		modname = "MetaEditorPanel";
		itemid = cmdl;
		itemname = IRm$.r1("I_META_EDIT");
		popt.uid = opt.uid;
		break;
	case "CMD_META_MIG":
		modname = "mgr_deploy";
		itemid = cmdl;
		itemname = IRm$.r1("I_META_MIG");
		break;
	case "CMD_RELATION_VIEW":
		itemid = cmdl;
		modname = "relationMgr";
		itemname = IRm$.r1("L_RELATION_VIEW");
		break;
	case "CMD_TABLE_REGISTER":
		itemid = cmdl;
		modname = "tableManager";
		itemname = IRm$.r1("L_MGR_DATABASE");
		modscr = "igcn";
		break;
	case "CMD_LOOKUP":
		itemid = cmdl;
		modname = "systemlookup";
		itemname = IRm$.r1("L_S_LK");
		break;
	case "CMD_CONNECT_DB":
		itemid = cmdl;
		modname = "mgrdb_config";
		itemname = IRm$.r1("L_REG_DATABASE");
		break;
	case "CMD_DATASET":
		itemid = cmdl;
		modname = "p$DataMgr";
		itemname = IRm$.r1("L_REG_DATASET");
		break;
	case "CMD_ABOUT":
		dlgpop = new IG$.aboutApp();
		IG$.checkLogin(null, dlgpop);
		break;
	case "CMD_USER_MANAGER":
		itemid = cmdl;
		modname = "mgrUser";
		itemname = IRm$.r1("L_MGR_USER");
		break;
	case "CMD_SYS_CONFIG":
		itemid = cmdl;
		modname = "mgrSysCfg";
		itemname = IRm$.r1("L_SYS_CONFIG");
		break;
	case "CMD_ASSIST_ROBO":
		itemid = cmdl;
		modname = "mgrColab";
		itemname = IRm$.r1("L_ASSIST_ROBO");
		break;
	case "CMD_VAR":
	case "CMD_BD_CONFIG":
		itemid = cmdl;
		modname = "hadoop_config";
		modscr = "igc9";
		itemname = IRm$.r1("I_BD_CONFIG");
		break;
	case "CMD_DBD_MGR":
		itemid = cmdl;
		modname = "mgr_dashboard";
		itemname = IRm$.r1("I_MGR_DBD");
		break;
	case "CMD_FEATURES":
		itemid = cmdl;
		modname = "mgrFeatures";
		break;
	case "CMD_REPORT_STYLE":
		itemid = cmdl;
		popt.title = IRm$.r1("T_GLOBAL_REPORT_STYLE");
		popt.styletype = "g";
		modname = "styleWizard";
		break;
	case "CMD_RSN":
		dlgpop = new IG$.rsn_editor({});
		IG$.checkLogin(null, dlgpop);
		break;
	case "CMD_SYSMON":
		modname = "sysmon";
		itemid = cmdl;
		popt.title = IRm$.r1("L_SYS_LOG");
		break;
	case "CMD_SYSRES":
		modname = "sys_resource";
		itemid = cmdl;
		break;
	case "CMD_SCHEDULE":
		itemid = cmdl;
		modname = "scheduler";
		itemname = IRm$.r1("I_SCHEDULE");
		break;
	case "CMD_SCH_PROG":
		itemid = cmdl;
		modname = "sch_prog";
		itemname = IRm$.r1("I_SCHEDULE_MOD");
		popt.mod_type = opt;
		
		if (opt == "single_sign_on")
		{
			itemname = "SingleSignOn";
		}
		break;
	case "CMD_CHART_DSGN":
		itemid = cmdl;
		modname = "chart_dsgn";
		itemname = "Chart Coder";
		break;
	case "CMD_HOME":
		IG$.changeApp(0);
		break;
	case "CMD_DATASET":
		IG$.changeApp(2);
		break;
	case "CMD_COMPETITION":
		IG$.changeApp(1);
		break;
	case "CMD_DISCUSSION":
		IG$.changeApp(3);
		break;
	case "CMD_WORKS":
		IG$.changeApp(4);
		break;
	case "CMD_BROWSER":
		var mexplorer = IG$.viewport.down("[name=mexplorer]");
		if (mexplorer && !mexplorer.isVisible())
		{
			IG$.changeApp(1);
		}
		break;
	case "CMD_TABPANEL":
		IG$.changeApp(2);
		break;
	case "CMD_WIZARD":
	case "CMD_APP_WIZARD":
		// IG$.changeApp(3);
		var doc = $(document),
			w = doc.width(),
			h = doc.height(),
			// dwizard = IG$.dwizard,
			dlg;
		
		if (ig$.$l_ > 5)
		{
			var lang = window.useLocale;
			lang = lang.substring(0, lang.indexOf("_"));
			window.open("../manual/" + lang, "_blank");
			return;
		}	
		if (!IG$.app_wizard && IG$.ltW)
		{
			IG$.app_wizard = new IG$.dlgSmartWizard({});
		}
		
		dlg = IG$.app_wizard;
		
		if (dlg)
		{
			dlg.setSize(w * 0.8, h * 0.8);
			IG$.showDlg(null, dlg);
		}
		break;
	case "CMD_SYNC_USER":
		itemid = cmdl;
		popt.uid = opt;
		modname = "sync_user";
		itemname = IRm$.r1("T_SYNC_USER");
		break;
	case "CMD_MN_MENUS":
		IG$.changeApp(4, opt);
		break;
	case "CMD_HD_BFLOW":
	case "CMD_MONG_DATA":
		btype = (cmd == "CMD_HD_BFLOW") ? "BigData" : "MongoData";
		mp && mp.navigateApp.call(mp, null, btype.toLowerCase(), btype, null, null, true);
		break;
	case "CMD_MONG_MAPR":
	case "CMD_HD_MAPR":
		itemid = cmdl;
		modname = "relationMgr";
		popt._cmode = 2;
		itemname = IRm$.r1("L_HD_BFLOW");
		break;
	case "CMD_ML_BAYES":
		var t = IG$._app_module_["bayesnet"];
		modname = t.name;
		modscr = t.modetype;
		itemname = itemname || t.opt.itemtype;
		if (t.opt)
		{
			for (k in t.opt)
			{
				popt[k] = t.opt[k];
			}
		}
		break;
	case "CMD_D_MENU":
		itemid = cmdl;
		modname = "dashboardmenu";
		itemname = cmdl;
		break;
	case "CMD_REVISION":
		itemid = cmdl;
		popt.uid = opt;
		modname = "mgrRevision";
		itemname = IRm$.r1("T_REVISION");
		break;
	case "CMD_TRASHBIN":
		itemid = cmdl;
		popt.uid = opt;
		modname = "mgr_trashbin";
		itemname = IRm$.r1("L_TRASHBIN");
		break;
	case "CMD_MG_DISCO":
		itemid = cmdl;
		popt.uid = opt;
		modname = "mg_disco";
		modscr = "mg_disco";
		itemname = IRm$.r1("T_MIG_DISCO");
		break;
	case "CMD_MG_UPGRADE":
		itemuid = cmdl;
		popt.uid = opt;
		modname = "mg_meta";
		modscr = "mg_meta";
		itemname = IRm$.r1("T_MIG_META_UPGRADE");
		break;
	case "CMD_NEW_REPORT":
		itemid = cmdl;
		modname = "ReportView";
		itemname = "Report";
		popt = popt || {};
		popt.writable = true;
		popt.opt_editmode = true;
		break;
	case "CMD_NEW_SQL_REPORT":
		itemid = cmdl;
		modname = "SQLReportView";
		itemname = "SQLReport";
		popt = popt || {};
		popt.writable = true;
		popt.opt_editmode = true;
		break;
	case "CMD_NEW_DASHBOARD":
		itemid = cmdl;
		modname = "DashboardEdit";
		modscr = "igc7";
		itemname = "Dashboard";
		break;
	case "CMD_MANUAL_AUTH":
		itemid = cmdl;
		modname = "MgrManual";
		modscr = "mg_meta";
		itemname = IRm$.r1("T_MGR_MANUAL");
		break;
	case "CMD_NEW_BOBJECT":
		IG$.create_meta();
		return;
	case "CMD_APP_HELP":
		var lang = "en";
		if (window.useLocale == "ko_KR")
		{
			lang = "ko";
		}
		window.open("../manual/" + lang + "/", "_blank");
		return;
//		break;
	}
	
	if (modname && IG$.mainPanel)
	{
		var tab = IG$.mainPanel.getComponent.call(IG$.mainPanel, itemid);
		
		if (tab)
		{
			IG$.mainPanel.setActiveTab.call(IG$.mainPanel, tab);
			
			if (cmdl == "cmd_revision")
			{
				tab.loadUID.call(tab, opt);
			}
		}
		else
		{
			if (IG$[modname])
			{
				panel = new IG$[modname](popt);
				if (!itemname)
				{
					itemname = panel.title;
				}
			}
			else
			{
				// load module
				scripts = ig$.bootconfig.scmap[modscr];
				
				IG$.getScriptCache(
					scripts, 
					new IG$.callBackObj(this, function() {
						if (IG$[modname])
						{
							panel = new IG$[modname](popt);
							if (!itemname)
							{
								itemname = panel.title;
							}
							
							panel.id = itemid
							panel.address = itemid;
							panel.title = IG$.getTabTitle(itemname);
							
							if (panel.isWindow == true)
							{
								IG$.checkLogin(null, panel);
							}
							else
							{
								var p = IG$.mainPanel.add.call(IG$.mainPanel, panel);
								IG$.mainPanel.setActiveTab.call(IG$.mainPanel, p);
							}
						}
						else
						{
							IG$.ShowError(IRm$.r1("L_ERR_L_MOD"));
						}
					})
				);
			}
			
			if (panel)
			{
				panel.id = itemid
				panel.address = itemid;
				panel.title = IG$.getTabTitle(itemname);
				
				if (panel.isWindow == true)
				{
					IG$.checkLogin(null, panel);
				}
				else
				{
					var p = IG$.mainPanel.add.call(IG$.mainPanel, panel);
					IG$.mainPanel.setActiveTab.call(IG$.mainPanel, p);
				}
			}
		}
	}
};
